<template>
	<div class="m-product-categroy">
		<dl class="classic">
			<dt></dt>
			<dt></dt>
			<dd v-for="item in classicList" :key="item.type">
				<m-select :name="item.title" :list="item.subList"/>
			</dd>
		</dl>
		<dl class="classic">
			<dt></dt>
			<dt></dt>
			<dd v-for="(item,index) in areaList" :key="index">
				<m-select :name="item.title" :list="item.subList"/>
			</dd>
		</dl>
	</div>
</template>

<script>
import MSelect from './select.vue'
export default {
	data(){
		return {
			classicList:[{

		        "title": "",

		        "type": "food",

		        "subList": [{

		            "name": "",

		            "id": "Japan"

		        }]

		    }, {

		        "title": "",

		        "type": "hotal",

		        "subList": [{

		            "name": "",

		            "id": "hot_spring"

		        }]

		    }],
		    areaList:[{
		        "title": "",
		        "subList": [{
		            "name": "",
		            "id": 120000
		        }, {
		            "name": "",
		            "id": 12222
		        }]

		    }],
		}
	},
	components:{
		MSelect,
	}
}
</script>


