import axios from 'axios'

axios.defaults.baseURL = 'https://api.duyiedu.com';
// 
axios.interceptors.request.use(function (config) {
    // 

    config.params = {
    	...config.params,
    	appkey: 'shannon_1584933284957'
    }
    return config;
  }, function (error) {
    // 
    return Promise.reject(error);
  });

export default axios;


