<template>
	<div class="m-istyle">
		<dl @mouseover="over" :class="nav.class">
			<dt>{{nav.title}}</dt>
			<dd v-for="(item,index) in nav.list" 
			:key="index" 
			:class="{active: kind == item.tab}" 
			:data-type="item.tab">{{item.text}}</dd>
			
		</dl>
		<ul class="ibody">
			<li v-for="(item,index) in resultaData[kind]" :key="index">
			    <el-card class="el-card" :body-style="{ padding: '0px' }" shadow="never">
			      <img :src="item.image" class="image">
			      <div class="cbody">
			        <div class="title" :title="item.title">{{item.title}}</div>
			        <div class="sub-title" :title="item.sub_title">{{item.sub_title}}</div>
			        <div class="price-info">
				        <span class="current-price-wrapper">
				        	<span class="price-symbol numfont"></span>
				        	<span class="current-price numfont">{{item.price}}</span>
				        </span>
				        <span class="sold bottom-right-info">{{item.address}}</span>
				       <!--  <span class="old-price">{{item.price_info.old_price}}</span>
				        <span class="sold bottom-right-info">{{item.address}}</span> -->
			        </div>
			       <!--  <div class="price-info" v-else-if="!item.rentNum">
				        <span class="current-price-wrapper">
				        	<span class="price-symbol numfont"></span>
				        	<span class="current-price numfont"></span>
				        </span>
			        </div>
			        <div class="price-info" v-else>
				        <span class="current-price-wrapper">
				        	<span class="price-symbol numfont"></span>
				        	<span class="current-price numfont">{{item.price_info.avg_price}}</span>
				        	<span class="units">/{{item.price_info.units}}</span>
				        </span>
			        </div> -->
			      </div>
			    </el-card>	  
			</li>
		</ul>
	</div>
</template>

<script>
import api from '@/api/index.js'
export default {
	data(){
		return {
			kind: 'all',
			resultaData: {},
			List: [{
				image: '//p0.meituan.net/bbia/850ce195a4db8d447644429ac6e32c20330100.jpg@294w_166h_1e_1c',
				tilte: '',
				sub_title: '616',
				price_info: {
					current_price: 58,
					old_price: 128,
					avg_price: null,
					units: null
				},
				rentNum:10,
				address: ''
			},{
				image: '//p0.meituan.net/msmerchant/455f8fe67ab4818446dda10061d394ee109929.jpg@294w_166h_1e_1c',
				tilte: '',
				sub_title: '441,8',
				price_info: {
					current_price: null,
					old_price: null,
					avg_price: 1299,
					units: ''
				},
				rentNum:10,
				address: ''
			}]
		}
	},
	props: [
		'nav'
	],
	created() {
		api.getResultProducts().then(res =>{

			this.resultaData = res.data.data;
		})
	},
	methods: {
		over(e){
			let dom = e.target;
			let tagName = dom.tagName.toLowerCase();
			if(tagName != 'dd'){
				return false;
			}
			this.kind = dom.getAttribute('data-type');

		}
	}

}
</script>

<style lang="scss">
	@import '@/assets/css/index/artistic.scss'
</style>


